:root{
  --bg-color: #333;
  --main-color: #08bd99;
}

.bg-container {
  background-color: var(--bg-color);
  color: var(--main-color);
  position: fixed;
  padding: 0;
  margin: 0;
  overflow: auto;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
}

#navbar {
  color: var(--main-color);
}

h3.text-center.mb-4 {
  color: var(--main-color);
}

div.card-body {
  background-color: whitesmoke;
  height: 20vh;
  overflow: hidden;
  color: var(--bg-color);
  padding: 12px;
}
div.col-md-3 {
  height: 30vh;
}

a.btn.btn-dark.btn-block {
  vertical-align: center;
  display: inline-block;
  float: none;
  color: var(--main-color);
}

h5.track-name {
  line-height: 2.5ex;
  height: 5.1ex;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: pre-wrap;
  line-clamp: 2;
  margin-bottom: 5px;
}

div.card-body.lyrics {
  display: inline-block;
  overflow: hidden;
  height: auto;
}

p.card-text.lyrics,
div.card-body.lyrics {
  color: var(--bg-color);
  background-color: whitesmoke;
  white-space: pre;
}

p.card-text.text-truncate {
  margin-bottom: 0;
}

div.card {
  border: none;
}

img.card-img {
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  
}

div.img-container {
  height: 0;
  overflow: hidden;
  padding-top: 100%;
  background: white;
  position: relative;
}

div.container-fluid {
  padding: 0 50px;
}

#searchbar{
    background-color: var(--bg-color)
}

i.fas.fa-search{
    color:white
}

button.btn.btn-outline-dark.btn-lg.rounded-circle:hover i.fas.fa-search{
    color:var(--main-color)
}


/* Create 5 column in a row */

.col-2dot4,
.col-sm-2dot4,
.col-md-2dot4,
.col-lg-2dot4,
.col-xl-2dot4 {
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
}
.col-2dot4 {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 20%;
  flex: 0 0 20%;
  max-width: 20%;
}
@media (min-width: 540px) {
  .col-sm-2dot4 {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 20%;
    flex: 0 0 20%;
    max-width: 20%;
  }
}
@media (min-width: 720px) {
  .col-md-2dot4 {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 20%;
    flex: 0 0 20%;
    max-width: 20%;
  }
}
@media (min-width: 960px) {
  .col-lg-2dot4 {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 20%;
    flex: 0 0 20%;
    max-width: 20%;
  }
}
@media (min-width: 1140px) {
  .col-xl-2dot4 {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 20%;
    flex: 0 0 20%;
    max-width: 20%;
  }
}
